import React from "react";

import {
    Button,
    Modal
} from "reactstrap";


export default class ConfirmDeleteModal extends React.Component {
    render() {
      const { isOpen, toggle, handle, type } = this.props;
        return(
          <Modal
            className="modal-dialog-centered"
            isOpen={isOpen}
            toggle={toggle}
          >
            <div className="modal-header">
              <h6 className="modal-title" id="modal-title-default">
                {`Confirm delete ${type}!`}
              </h6>
              <button
                aria-label="Close"
                className="close"
                data-dismiss="modal"
                type="button"
                onClick={toggle}
              >
                <span aria-hidden={true}>Ã—</span>
              </button>
            </div>
            <div className="modal-body">
              <p>
                {`Do you want to delete this ${type}?`}
              </p>
            </div>
            <div className="modal-footer">
              <Button color="danger" type="button" onClick={handle}>
                Delete
              </Button>
              <Button
                className="ml-auto"
                color="link"
                data-dismiss="modal"
                type="button"
                onClick={toggle}
              >
                Close
              </Button>
            </div>
        </Modal>)
    }
}
