import React from "react";
import { connect } from "react-redux";
// nodejs library that concatenates classes
import { Link } from "react-router-dom";

// reactstrap components
import ReactDatetime from "react-datetime";
import _ from "lodash";
import { toast } from "react-toastify";
import SVG from "svg.js";
import {
  Button,
  Card,
  CardImg,
  CardHeader,
  Label,
  CardBody,
  FormGroup,
  Form,
  Container,
  Row,
  Col,
  Table,
} from "reactstrap";
// core components

import {
  getTime,
  getDate,
  getMomentObject,
  isGTENowDay,
  convertToDecimal,
  getImageMeta,
} from "helpers/UtilsHelper";
import { FormInput } from "components/Form";

import venueApi from "api/venueApi";
import { removeVenue } from "../../../redux/actions/venuesActions";
import ConfirmDeleteModal from "./ConfirmDeleteModal.jsx";

import "./Venues.scss";

const startTimeDefault = "8:00";
const closeTimeDefault = "20:00";
const dayOnWeek = [
  "monday",
  "tuesday",
  "wednesday",
  "thursday",
  "friday",
  "saturday",
  "sunday",
];
const operatingHoursData = dayOnWeek.map((item, index) => {
  return {
    dayOfWeek: index + 1,
    name: item,
    startTime: startTimeDefault,
    closeTime: closeTimeDefault,
    isUsable: false,
  };
});

class VenueCreate extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: null,
      image: null,
      roomMap: null,
      address: null,
      phoneNumber: null,
      email: null,
      contactPerson: null,
      operatingHours: operatingHoursData,
      blackoutDates: [],
      errors: {},
      isFormReady: {},
      isDeleteVenue: false,
    };
  }

  deleleVenue = () => {
    const { venueId } = this.props.match.params;
    venueApi
      .delete(venueId)
      .then(() => {
        toast.success("Delete venue successfully");
        this.props.history.push("/admin/venues");
      })
      .catch(() => {
        toast.error("Delete venue failed");
      });
  };

  toggleConfirmDelete = () => {
    this.setState({ isDeleteVenue: !this.state.isDeleteVenue });
  };

  addBlackoutDates = (data) => {
    this.setState({
      blackoutDates: [{ date: null }, ...this.state.blackoutDates],
    });
  };

  setBlackoutDates = (e, index) => {
    const date = new Date(e._d + "");
    let tempData = this.state.blackoutDates;
    tempData[index] = { date: getDate(date) };
    this.setState({ blackoutDates: tempData });
  };

  deleteBlackoutDates = (index) => {
    let tempArr = this.state.blackoutDates;
    tempArr.splice(index, 1);
    this.setState({ blackoutDates: tempArr });
  };

  blackoutDateValidate = (currentDate) => {
    const { blackoutDates } = this.state;
    let check = false;
    blackoutDates.forEach((item) => {
      check = check || getDate(currentDate) === item.date;
    });
    return isGTENowDay(currentDate) && !check;
  };

  handleChange = (e) => {
    const { name, value } = e.target;
    delete this.state.errors[name];
    this.setState({ [name]: value });
  };

  handleChangeTime = (e, index, type) => {
    const date = new Date(e._d + "");
    if (
      type === "closeTime" &&
      date.getHours() === 0 &&
      date.getMinutes() === 0
    ) {
      date.setHours(23);
      date.setMinutes(59);
    }

    let tempData = this.state.operatingHours;
    tempData[index][type] = getTime(date, "HH:mm");
    this.setState({ operatingHours: tempData });
  };

  handleCheckTime = (e) => {
    let tempData = this.state.operatingHours;
    const { name, checked } = e.target;
    tempData[Number(name)].isUsable = checked;
    this.setState({ operatingHours: tempData });
  };

  handleFile = (event) => {
    const { name, files } = event.target;
    if (files.length) {
      this.setState({
        [name]: files[0],
      });
    }
  };

  handleError = (field, status) => {
    let check = true;
    if (status) {
      for (let key in this.state.isFormReady) {
        check = check && this.state.isFormReady[key];
      }
    }
    this.setState({
      isFormReady: {
        ...this.state.isFormReady,
        [field]: status,
        check: check && status,
      },
    });
  };

  saveForm = (e) => {
    e.preventDefault();
    const { venueId } = this.props.match.params;
    const submitData = this.state;
    if (_.isString(submitData.image)) {
      delete submitData["image"];
    }
    if (_.isString(submitData.roomMap)) {
      delete submitData["roomMap"];
    }
    let formData = new FormData();
    Object.keys(submitData).forEach((key) => {
      if (Array.isArray(this.state[key])) {
        formData.append([key], JSON.stringify(this.state[key]));
      } else {
        formData.append([key], this.state[key]);
      }
    });
    let smsPass,
      smsFail,
      apiCall = null;
    if (venueId) {
      apiCall = venueApi.update({ formData, venueId });
      smsPass = "Update venue successfully";
      smsFail = "Update venue failed";
    } else {
      apiCall = venueApi.create(formData);
      smsPass = "Create venue successfully";
      smsFail = "Create venue failed";
    }
    apiCall
      .then((res) => {
        toast.success(smsPass);
        if (!venueId) {
          this.props.history.push(`/admin/venues/${res.id}`);
        }
      })
      .catch((err) => {
        const errors = err.response.data;
        this.setState({ errors });
        toast.error(smsFail);
      });
  };

  getVenueId() {
    const { venueId } = this.props.match.params;
    return venueId;
  }

  async drawSVG(mapImage, mapData) {
    const roomMapWrapper = document.getElementById("room-map-wrapper");
    const { width, height } = await getImageMeta(mapImage);
    const parentWidth = roomMapWrapper ? roomMapWrapper.clientWidth : 500;
    const parentHeight = convertToDecimal((height / width) * parentWidth, 4);
    const weightWidth = parentWidth / width;
    const weightHeight = parentHeight / height;

    let draw = SVG("svg-drawing").size(parentWidth, parentHeight);
    draw.image(mapImage, parentWidth, parentHeight);
    try {
      JSON.parse(mapData).forEach((item) => {
        let pointArr, childItem;
        childItem = draw.link(
          `#/admin/venues/${this.getVenueId()}/spaces/${item.id}`
        );
        const listPoint = item.coords;
        switch (item.shape) {
          case "poly":
            pointArr = [];
            for (let i = 0; i < listPoint.length; i += 2) {
              const pointData = [
                listPoint[i] * weightWidth,
                listPoint[i + 1] * weightHeight,
              ];
              pointArr.push(pointData);
            }
            childItem.polyline(pointArr);
            break;
          case "rect":
            const x = (listPoint[2] - listPoint[0]) * weightWidth;
            const y = (listPoint[3] - listPoint[1]) * weightHeight;
            childItem.rect(x, y).attr({
              x: listPoint[0] * weightWidth,
              y: listPoint[1] * weightHeight,
            });
            break;
          default:
            break;
        }
        childItem
          .attr({ fill: "transparent" })
          .mouseover(function () {
            this.fill({ color: "rgba(0,0,0,0.5)" });
          })
          .mouseout(function () {
            this.fill({ color: "transparent" });
          });
      });
    } catch (e) {}
  }

  componentDidMount() {
    const { venueId } = this.props.match.params;
    if (venueId) {
      venueApi.detail(venueId).then((res) => {
        const {
          name,
          image,
          roomMap,
          roomMapMapping,
          address,
          phoneNumber,
          email,
          contactPerson,
          operatingHours,
          blackoutDates,
        } = res;
        const operatingHoursUpdated = operatingHours.map((item, index) => {
          item.name = dayOnWeek[index];
          return item;
        });
        if (roomMap && roomMapMapping && roomMapMapping.length) {
          this.drawSVG(roomMap, roomMapMapping);
        }
        this.setState({
          name,
          image,
          roomMap,
          address,
          phoneNumber,
          email,
          contactPerson,
          operatingHours: operatingHoursUpdated,
          blackoutDates,
        });
      });
    }
  }

  render() {
    const {
      name,
      email,
      address,
      contactPerson,
      phoneNumber,
      blackoutDates,
      image,
      operatingHours,
      roomMap,
      errors,
      isFormReady,
    } = this.state;
    const formValid =
      name &&
      email &&
      image &&
      roomMap &&
      !_.isEmpty(blackoutDates) &&
      !_.isEmpty(operatingHours) &&
      isFormReady.check;
    const { venueId } = this.props.match.params;
    return (
      <>
        <ConfirmDeleteModal
          className="modal-dialog-centered"
          isOpen={this.state.isDeleteVenue}
          toggle={this.toggleConfirmDelete}
          handle={this.deleleVenue}
          type="Vanue"
        />
        <Container className="venues mt--12" fluid>
          <Card className="mb-4">
            <CardHeader>
              <h3 className="mb-0">Create Venue</h3>
            </CardHeader>
            <CardBody>
              <Form onSubmit={this.saveForm}>
                <Row>
                  <Col md="6">
                    <FormGroup className="row">
                      <Label className="form-control-label" md="3">
                        Venue Name
                      </Label>
                      <Col md="7">
                        <FormInput
                          required={true}
                          type="text"
                          name="name"
                          defaultValue={name}
                          handleError={this.handleError}
                          errors={errors["name"]}
                          onChange={this.handleChange}
                        />
                      </Col>
                    </FormGroup>
                    <FormGroup className="row">
                      <Label className="form-control-label" md="3">
                        Image
                      </Label>
                      <Col md="7">
                        <Card className="align-items-center">
                          {image ? (
                            <CardImg
                              alt="..."
                              src={
                                _.isObject(image)
                                  ? URL.createObjectURL(image)
                                  : image
                              }
                              top
                            />
                          ) : (
                            <div
                              className="venues__placeholder-image"
                              onClick={() => this.openImageFile.click()}
                            >
                              <div className="venues__placeholder-image__label">
                                No Image
                              </div>
                            </div>
                          )}
                          <input
                            ref={(ref) => (this.openImageFile = ref)}
                            onChange={this.handleFile}
                            style={{ display: "none" }}
                            type="file"
                            name="image"
                            id="roomImage"
                            accept=".jpg,.gif,.jpeg,.png"
                          />
                          <Button
                            className="mt-3 w-30"
                            color="default"
                            onClick={() => this.openImageFile.click()}
                          >
                            Select Image
                          </Button>
                          <div className="error-text mt-3">
                            {errors["image"]}
                          </div>
                        </Card>
                      </Col>
                    </FormGroup>
                    <FormGroup className="row">
                      <Label className="form-control-label" md="3">
                        Room Map
                      </Label>
                      <Col md="7" id="room-map-wrapper" className="p-0">
                        {this.getVenueId() ? (
                          <div id="svg-drawing"></div>
                        ) : (
                          <Card className="align-items-center">
                            <input
                              ref={(ref) => (this.openMapFile = ref)}
                              onChange={this.handleFile}
                              style={{ display: "none" }}
                              type="file"
                              name="roomMap"
                              id="roomMap"
                              accept=".zip"
                            />
                            <div className="d-flex w-100 justify-content-between align-items-center">
                              <div
                                className="nav-link"
                                style={{ fontSize: ".85rem" }}
                              >
                                {roomMap && roomMap.name
                                  ? roomMap.name
                                  : "No room map zip file!"}
                              </div>
                              <Button
                                color="default"
                                onClick={() => this.openMapFile.click()}
                              >
                                Select Mapping
                              </Button>
                            </div>
                            {errors && errors["roomMap"] && (
                              <div className="error-text mt-3">
                                {errors["roomMap"]}
                              </div>
                            )}
                          </Card>
                        )}
                      </Col>
                    </FormGroup>
                  </Col>
                  <Col md="6">
                    <FormGroup className="row">
                      <Label className="form-control-label" md="3">
                        Address
                      </Label>
                      <Col md="7">
                        <FormInput
                          type="text"
                          name="address"
                          defaultValue={address}
                          handleError={this.handleError}
                          errors={errors["address"]}
                          onChange={this.handleChange}
                        />
                      </Col>
                    </FormGroup>
                    <FormGroup className="row">
                      <Label
                        className="form-control-label"
                        htmlFor="example-search-input"
                        md="3"
                      >
                        Tel
                      </Label>
                      <Col md="7">
                        <FormInput
                          type="text"
                          name="phoneNumber"
                          defaultValue={phoneNumber}
                          handleError={this.handleError}
                          errors={errors["phoneNumber"]}
                          onChange={this.handleChange}
                          regex="phoneNumber"
                        />
                      </Col>
                    </FormGroup>
                    <FormGroup className="row">
                      <Label
                        className="form-control-label"
                        htmlFor="example-email-input"
                        md="3"
                      >
                        E-mail
                      </Label>
                      <Col md="7">
                        <FormInput
                          required
                          type="email"
                          name="email"
                          defaultValue={email}
                          handleError={this.handleError}
                          errors={errors["email"]}
                          onChange={this.handleChange}
                          regex="email"
                        />
                      </Col>
                    </FormGroup>
                    <FormGroup className="row">
                      <Label
                        className="form-control-label"
                        htmlFor="example-password-input"
                        md="3"
                      >
                        Contact person
                      </Label>
                      <Col md="7">
                        <FormInput
                          type="text"
                          name="contactPerson"
                          defaultValue={contactPerson}
                          handleError={this.handleError}
                          errors={errors["contactPerson"]}
                          onChange={this.handleChange}
                        />
                      </Col>
                    </FormGroup>
                    <FormGroup className="row">
                      <Label
                        className="form-control-label"
                        htmlFor="example-password-input"
                        md="12"
                        lg="3"
                      >
                        Operating Hours
                      </Label>
                      <Col className="venues__openrating-hours" md="12" lg="7">
                        <Table className="venues__table-openrating-hours w-100">
                          <thead>
                            <tr>
                              <th width="50%"></th>
                              <th width="25%">Open</th>
                              <th width="25%">Close</th>
                            </tr>
                          </thead>
                          <tbody>
                            {operatingHours.map((item, index) => (
                              <tr key={index}>
                                <td>
                                  <div className="custom-control custom-checkbox">
                                    <input
                                      className="custom-control-input"
                                      id={item.name}
                                      name={index}
                                      type="checkbox"
                                      checked={item.isUsable}
                                      onChange={this.handleCheckTime}
                                    />
                                    <label
                                      className="custom-control-label text-capitalize"
                                      htmlFor={item.name}
                                    >
                                      {item.name}
                                    </label>
                                  </div>
                                </td>
                                <td>
                                  <ReactDatetime
                                    className="venues__select-time"
                                    value={getMomentObject(
                                      item.startTime,
                                      "HH:mm"
                                    )}
                                    timeFormat={true}
                                    dateFormat={false}
                                    closeOnSelect={true}
                                    onChange={(event) =>
                                      this.handleChangeTime(
                                        event,
                                        index,
                                        "startTime"
                                      )
                                    }
                                  />
                                </td>
                                <td>
                                  <ReactDatetime
                                    className="venues__select-time"
                                    value={getMomentObject(
                                      item.closeTime,
                                      "HH:mm"
                                    )}
                                    closeOnSelect={true}
                                    timeFormat={true}
                                    dateFormat={false}
                                    onChange={(event) =>
                                      this.handleChangeTime(
                                        event,
                                        index,
                                        "closeTime"
                                      )
                                    }
                                  />
                                </td>
                              </tr>
                            ))}
                          </tbody>
                        </Table>
                        <div className="error-text mt-3">
                          {errors["operatingHours"]}
                        </div>
                      </Col>
                    </FormGroup>
                    <FormGroup className="row mt-5">
                      <Label
                        className="form-control-label"
                        htmlFor="example-password-input"
                        md="3"
                      >
                        Blackout Dates
                      </Label>
                      <Col
                        md="7"
                        className={`venues__blackout-date ${
                          blackoutDates.length > 4
                            ? "venues__blackout-date--scroll"
                            : ""
                        }`}
                      >
                        <Row className="mb-3">
                          <Col lg="6">
                            <Button
                              className="w-5"
                              color="secondary"
                              onClick={this.addBlackoutDates}
                            >
                              &#x2b;
                            </Button>
                          </Col>
                        </Row>
                        <div className="venues__blackout-date-section">
                          {blackoutDates.map((item, index) => (
                            <Row key={index} className="mb-2">
                              <Col lg="6" sm="8" xs="8">
                                <ReactDatetime
                                  inputProps={{
                                    placeholder: "Date Picker",
                                  }}
                                  closeOnSelect={true}
                                  timeFormat={false}
                                  dateFormat={true}
                                  value={getMomentObject(
                                    item.date,
                                    "YYYY:MM:DD"
                                  )}
                                  onChange={(event) =>
                                    this.setBlackoutDates(event, index)
                                  }
                                  isValidDate={this.blackoutDateValidate}
                                />
                              </Col>
                              <Col md="2" xs="2">
                                <Button
                                  className="w-5"
                                  color="secondary"
                                  onClick={() =>
                                    this.deleteBlackoutDates(index)
                                  }
                                >
                                  &#8722;
                                </Button>
                              </Col>
                            </Row>
                          ))}
                        </div>
                        <div className="error-text mt-3">
                          {errors["blackoutDates"]}
                        </div>
                      </Col>
                    </FormGroup>
                  </Col>
                </Row>
                <Row>
                  <Col md="6" className="mb-3">
                    {venueId && (
                      <Button color="danger" onClick={this.toggleConfirmDelete}>
                        Delete Venue
                      </Button>
                    )}
                  </Col>
                  <Col md="6" className="mb-3">
                    <Link to={"/admin/venues"} className="mr-3">
                      <Button color="secondary">Cancel</Button>
                    </Link>
                    <Button
                      color="success"
                      onClick={this.saveForm}
                      disabled={false && formValid}
                    >
                      {this.getVenueId() ? "Save Changes" : "Create Venue"}
                    </Button>
                  </Col>
                </Row>
              </Form>
            </CardBody>
          </Card>
        </Container>
      </>
    );
  }
}

const mapActionToProps = { removeVenue };

export default connect(null, mapActionToProps)(VenueCreate);
